import "./root.scss"
import React from "react";
import Home from "./components/pages/Home";
import {Route, Routes, Navigate} from "react-router-dom";
import PostInfo from "./components/pages/PostInfo";
import Works from "./components/pages/Works";
import Profile from "./components/pages/Profile";
import About from "./components/pages/About";
import WithSidebar from "./utils/WithSidebar";
import WithoutSidebar from "./utils/WithoutSidebar";
import Login from "./components/pages/Login";
import Register from "./components/pages/Register";
import ModalStory from "./components/ModalStory";
import {useSelector} from "react-redux";
import ModalImage from "./components/ModalImage";
import Search from "./components/pages/Search";
import {authSelect} from "./store/slices/authSlice";
import {useAppSelector} from "./store";

function App() {
    const showModal = useAppSelector((state) => state.story.showModal)
    const {isAuth} = useAppSelector(authSelect)
    const imageModal = useAppSelector((state) => state.post.imageModal)
    return (
        <div className={`App ${showModal === true ? 'block' : ''}`}>
            {showModal &&
                <ModalStory/>
            }
            {imageModal &&
                <ModalImage/>
            }
            {isAuth ?
(                <Routes>
                    <Route path={'*'} element={
                        <WithSidebar>
                            <Route path={'/'} element={<Home/>}/>
                            <Route path={'/post/:postId'} element={<PostInfo/>}/>
                            <Route path={'/works'} element={<Works/>}/>
                            <Route path={'/profile'} element={<Profile/>}/>
                            <Route path={'/search'} element={<Search/>}/>
                            <Route path="/*" element={<Navigate to="/" replace={true}/>}/>
                        </WithSidebar>}>
                        <Route path={'home'} element={<Home/>}/>
                        <Route path={'post/:postId'} element={<PostInfo/>}/>
                        <Route path={'works'} element={<Works/>}/>
                        <Route path={'profile'} element={<Profile/>}/>
                        <Route path={'search'} element={<Search/>}/>
                        <Route path="/*" element={<Navigate to="/" replace={true}/>}/>
                    </Route>
                    <Route path={'*'} element={
                        <WithoutSidebar>
                            <Route path={'/about'} element={<About/>}/>
                            <Route path={'/login'} element={<Login/>}/>
                            <Route path={'/register'} element={<Register/>}/>
                        </WithoutSidebar>}>
                        <Route path={'about'} element={<About/>}/>
                        <Route path={'login'} element={<Login/>}/>
                        <Route path={'register'} element={<Register/>}/>
                    </Route>
                </Routes>)
                : (
                    <Routes>
                        <Route path={'*'} element={
                            <WithSidebar>
                                <Route path={'/'} element={<Home/>}/>
                                <Route path={'/post/:postId'} element={<PostInfo />}/>
                                <Route path={'/works'} element={<Works/>}/>
                                <Route path={'/search'} element={<Search/>}/>
                                <Route path="/*" element={<Navigate to="/" replace={true}/>}/>
                            </WithSidebar>}>
                            <Route path={'home'} element={<Home/>}/>
                            <Route path={'post/:postId'} element={<PostInfo/>}/>
                            <Route path={'works'} element={<Works/>}/>
                            <Route path={'search'} element={<Search/>}/>
                            <Route path="/*" element={<Navigate to="/" replace={true}/>}/>
                        </Route>
                        <Route path={'*'} element={
                            <WithoutSidebar>
                                <Route path={'/about'} element={<About/>}/>
                                <Route path="/*" element={<Navigate to="/" replace={true}/>}/>
                            </WithoutSidebar>}>
                            <Route path={'about'} element={<About/>}/>
                            <Route path="/*" element={<Navigate to="/" replace={true}/>}/>
                        </Route>
                    </Routes>
                )
            }


        </div>
    );
}




export default App;
